	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://danmaq.com/image/illustrations/mono/2004-2007/khan_s.jpg\" alt=\"One shot Merry\"></p>\n<p>Mary drew one depiction as a drowsiness countermeasure at C 73. Because there was no Mary 's material, I tried to draw it with only the source in the brain. I thought that it was absolutely wrong somewhere, but it is safe if there is no fatal mistake when I check it. (I was wondering whether I should go ophthalmologically seriously when I drew Rumia as before.)</p>\n<p>I think that the line became pretty cleaner than before, but still it is still dirty orz</p>","frontmatter":{"cover":null,"date":"2008-01-01T23:00:00+09:00","strDate":"2008/1/1","title":"[Toho] One drawing Merry"}}},"pathContext":{"path":"/en/blog/2008/01/01-東方-一発描きメリー/","langKey":"en"}}

